.ubp-color-component{
    padding: 16px;
    width: 260px;
}
.ubp-color-container{
    margin-bottom: 20px;
}

h2.ubp-h2-heading {
    color: #747678;
    padding: 10px 10px;
}

.ubp-grid-product-block{
    padding: 35px;
}

.ubp-productlist .components-flex-item.wc-block-layout{
    padding: 10px;
}
.ubp-productlist.has-1-columns .components-flex .components-flex-item {
    flex: 1 0 100%;
    max-width: 100%
}

.ubp-productlist.has-2-columns .components-flex .components-flex-item {
    flex: 1 0 50%;
    max-width: 50%
}

.ubp-productlist.has-3-columns .components-flex .components-flex-item{
    flex: 2 0 33.3333333333%;
    max-width: 33.3333333333%
}

.ubp-productlist.has-4-columns .components-flex .components-flex-item {
    flex: 1 0 25%;
    max-width: 25%;
}

.ubp-productlist.has-5-columns .components-flex .components-flex-item {
    flex: 1 0 20%;
    max-width: 20%
}

.ubp-productlist.has-6-columns .components-flex .components-flex-item{
    flex: 1 0 16.6666666667%;
    max-width: 16.6666666667%
}

.ubp-productlist.has-7-columns .components-flex .components-flex-item {
    flex: 1 0 14.2857142857%;
    max-width: 14.2857142857%
}

.ubp-productlist.has-8-columns .components-flex .components-flex-item {
    flex: 1 0 12.5%;
    max-width: 12.5%
}

@media only screen and (max-device-width: 480px) {
    .ubp-productlist .components-flex .components-flex-item  {
    flex: 1 0 50%;
    max-width: 50%

