}
}

// pagination css
.ubp-pagi-container {
    --primary: #23adad;
    --greyLight: #23adade1;
    --greyLight-2: #cbe0dd;
    --greyDark: #2d4848;
  }

.ubp-pagi-container ul {
    list-style-type: none;
}

.ubp-pagi-container .ubp-pagi {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 5rem;
    border-radius: 0.6rem;
    background: #fff;
    box-shadow: 0 0.8rem 2rem rgba(90, 97, 129, 0.05);
}
.ubp-pagi__numbers, .ubp-pagi__btn, .ubp-pagi__dots {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0.8rem;
    font-size: 1.4rem;
    cursor: pointer;
}
.ubp-pagi__dots {
    width: 2.6rem;
    height: 2.6rem;
    color: var(--greyLight);
    cursor: initial;
}
.ubp-pagi__numbers {
    width: 2.6rem;
    height: 2.6rem;
    border-radius: 0.4rem;
}
.ubp-pagi__numbers:hover {
    color: var(--primary);
}
.ubp-pagi__numbers.active {
    color: #fff;
    background: var(--primary);
    font-weight: 600;
    border: 1px solid var(--primary);
}
.ubp-pagi__btn {
    color: var(--greyLight);
    pointer-events: none;
}
.ubp-pagi__btn.active {
    color: var(--greyDark);
    pointer-events: initial;
}
.ubp-pagi__btn.active:hover {
    color: var(--primary);
}